@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Geist+Mono:wght@100..900&family=Geist:wght@100..900&family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap');

* {
    font-family: "Geist", sans-serif;
    box-sizing: border-box;
}

.font-nunito {
    font-family: "Nunito Sans", sans-serif;
    font-weight: 600;
}



html, body, #root {
    height: auto;
    overflow: auto;
    margin: 0;
    padding: 0;
    overscroll-behavior: none;
    -webkit-overflow-scrolling: touch;
}
  

::-webkit-scrollbar {
    width: 0.6rem;
    height: 0rem;
  }
  
  ::-webkit-scrollbar-track {
    background: #ffffff;
  }
  

  ::-webkit-scrollbar-thumb {
    background: #A6F750;
  }

  


  .clip-path-mountain {
    clip-path: polygon(0% 100%, 50% 50%, 100% 100%);
  }
  

  @keyframes neon-glow {
    0% { text-shadow: 0 0 5px #A6F750, 0 0 10px #A6F750, 0 0 15px #A6F750; }
    50% { text-shadow: 0 0 10px #C5F494, 0 0 20px #C5F494, 0 0 30px #C5F494; }
    100% { text-shadow: 0 0 5px #A6F750, 0 0 10px #A6F750, 0 0 15px #A6F750; }
  }
  
  .neon-title {
    animation: neon-glow 2s infinite alternate;
  }
  
  .text-glow {
    text-shadow: 0 0 8px rgba(255, 255, 255, 0.5);
  }
  
  @keyframes floating {
    0% { transform: translateY(0px) rotate(0deg); }
    50% { transform: translateY(-8px) rotate(2deg); }
    100% { transform: translateY(0px) rotate(0deg); }
  }
  
  .floating-img {
    animation: floating 5s ease-in-out infinite;
  }
  
  @keyframes glow-effect {
    0% { box-shadow: 0 0 10px rgba(166, 247, 80, 0.3); }
    50% { box-shadow: 0 0 20px rgba(166, 247, 80, 0.6); }
    100% { box-shadow: 0 0 10px rgba(166, 247, 80, 0.3); }
  }
  
  

  @keyframes light-swipe {
    0% { left: -120%; }
    10% { left: 120%; }
    100% { left: 120%; }
  }
  .glow-btn {
    position: relative;
    overflow: hidden;
  }

  .glow-btn::before {
    content: "";
    position: absolute;
    top: 0;
    left: -120%;
    width: 60px;
    height: 100%;
    background: rgba(255, 255, 255, 0.6);
    transform: skewX(-30deg);
    animation: light-swipe 2.5s infinite ease-in-out;
  }



  
  
  